import React, {Component} from 'react';
import {List, Card, Icon, Button, Modal, message} from "antd";

import axios from 'axios';
import moment from 'moment';

import EditTextMaterialModal from '../components/EditTextMaterialModal';

import "./index.scss";

class TextMaterial extends Component {

  constructor(props) {
    super(props);
    this.openEditMaterialModal = this.openEditMaterialModal.bind(this);
    this.submitTextMaterial = this.submitTextMaterial.bind(this);
    this.cancelEditMaterial = this.cancelEditMaterial.bind(this);
    this.updateTextMaterial = this.updateTextMaterial.bind(this);
  }

  state = {
    editMaterialModalVisible: false,
    textMaterialList: [],
    initMaterial: {},
    index: -1
  };

  openEditMaterialModal() {
    this.setState({editMaterialModalVisible: true})
  }

  submitTextMaterial(textMaterial) {
    const {initMaterial: {_id = ""}, index, textMaterialList} = this.state;
    textMaterial.timing_time = + textMaterial.timing_time;
    textMaterial.code = this.props.code;
    _id && (textMaterial._id = _id);
    Modal.confirm({
      title: "确定提交这条文本素材吗？",
      onOk: () => {
        if(!_id) {
          axios.post('/textMaterial', {...textMaterial})
            .then(res => {
              const {code, msg, data} = res.data;
              if(code === 1) {
                this.setState(prevState => ({textMaterialList: [...prevState.textMaterialList, data]}));
                message.success(msg);
                this.cancelEditMaterial();
              } else {
                message.warning(msg);
              }
            })
        } else {
          axios.put('/textMaterial', {...textMaterial})
            .then(res => {
              const {code, msg, data} = res.data;
              if(code === 1) {
                textMaterialList.splice(index, 1, data);
                this.setState({textMaterialList});
                message.success(msg);
                this.cancelEditMaterial();
              } else {
                message.warning(msg);
              }
            })
        }
      }
    });
  }

  cancelEditMaterial() {
    this.setState({editMaterialModalVisible: false, initMaterial: {}})
  }

  updateTextMaterial(item, index) {
    this.openEditMaterialModal();
    this.setState({
      initMaterial: {...item}, index
    })
  }

  sendMaterial(item, index) {
    let {textMaterialList} = this.state;
    Modal.confirm({
      title: "是否发送这条素材",
      onOk: () => {
        axios.post("/textMaterial/send", {...item})
          .then(res => {
            const { code, msg } = res.data;
            if(code === 1) {
              message.success(msg);
              Object.assign(textMaterialList[index], {isSend: 1})
            } else {
              message.warning(msg);
            }
          })
      }
    })
  }

  deleteMaterial(_id, index) {
    Modal.confirm({
      title: "是否删除这条素材？",
      onOk: () => {
        axios.delete("/textMaterial", {params: {_id}})
          .then(res => {
            const {code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              this.setState(prevState => {
                let textMaterialList = prevState.textMaterialList;
                textMaterialList.splice(index, 1);
                return {textMaterialList}
              })
            } else {
              message.warning(msg)
            }
          })
      }
    })
  }

  componentDidMount() {
    axios.get("/textMaterial", {params: {code: this.props.code}})
      .then(res => {
        const {code, msg, data} = res.data;
        code === 1 && this.setState({textMaterialList: data})
      })
  }

  render() {
    const {editMaterialModalVisible, textMaterialList, initMaterial} = this.state;
    const {code} = this.props;
    return (
      <div className="text-material">
        <Button className="mb15" onClick={this.openEditMaterialModal}>创建文本素材</Button>
        <List grid={{gutter: 16, xl: 3, xxl: 6,}}
              dataSource={textMaterialList}
              renderItem={(item, index) => (
                <List.Item key={item._id}>
                  <Card title={item.is_timing ? <p>于 <span className="date">{moment(item.timing_time).format("YYYY-MM-DD HH:mm")}</span> 发送</p> : "未设置定时时间"} actions={item.isSend ? [<span className="see-msg-id" onClick={() => Modal.warning({title: "msg_id", content: <p style={{userSelect: "text"}}>{item.msg_id}</p>})}><Icon type="eye" key="eye"/><span>查看msg_id</span></span>] : [
                    <Icon type="setting" key="setting" onClick={() => this.updateTextMaterial(item, index)} />,
                    <Icon disabled={true} type="share-alt" key="edit" onClick={() => item.is_timing ? message.warning("已设置为定时发送，如有需要，请点击修改") : this.sendMaterial(item, index)}/>,
                    <Icon type="delete" key="delete" onClick={() => this.deleteMaterial(item._id, index)} />
                  ]}>
                    <p className="card-content1">{item.content}</p>
                  </Card>
                </List.Item>
              )}
        />
        {editMaterialModalVisible && <EditTextMaterialModal initMaterial={initMaterial} editMaterialModalVisible={editMaterialModalVisible} cancelEditMaterial={this.cancelEditMaterial} code={code} submitTextMaterial={this.submitTextMaterial} />}
      </div>
    )
  }
}

export default TextMaterial;